<template>
  <div id="app">
    <vs-button @click="visibleAlert = true">Alert</vs-button>
    <vs-button @click="visibleConfirm = true">Confirm</vs-button>
    <vs-button @click="visiblePrompt = visibleAlert = true">Prompt</vs-button>
    <vs-dialog width="550px" not-center v-model="visibleAlert">
      <template #header>
        <h4 class="not-marginvisible">
          Welcome to
          <b>Vuesax</b>
        </h4>
      </template>
      <template #footer>
        <div class="con-footer">
          <vs-button @click="onCloseAlert">Ok</vs-button>
        </div>
      </template>
    </vs-dialog>
    <vs-dialog width="550px" not-center v-model="visibleConfirm">
      <template #header>
        <h4 class="not-margin">
          Welcome to
          <b>Vuesax</b>
        </h4>
      </template>

      <template #footer>
        <div class="con-footer">
          <vs-button @click="onCloseConfirm">Ok</vs-button>
          <vs-button @click="onCloseConfirm" dark>Cancel</vs-button>
        </div>
      </template>
    </vs-dialog>
    <vs-dialog width="300px" not-center v-model="visiblePrompt">
      <template #header>
        <h4 class="not-margin">
          Welcome what is your
          <b>Name</b>
        </h4>
      </template>

      <div class="con-content">
        <vs-input placeholder="Name"></vs-input>
      </div>

      <template #footer>
        <div class="con-footer">
          <vs-button @click="onClosePrompt">Ok</vs-button>
          <vs-button @click="onClosePrompt" dark>Cancel</vs-button>
        </div>
      </template>
    </vs-dialog>
    <native-back
      :state="[visibleAlert, visibleConfirm, visiblePrompt]"
      :closeMethods="[onCloseAlert, onCloseConfirm, onClosePrompt]"
    />
  </div>
</template>

<script>
export default {
  name: 'App',
  data() {
    return {
      visibleAlert: false,
      visibleConfirm: false,
      visiblePrompt: false
    };
  },
  methods: {
    onCloseAlert() {
      this.visibleAlert = false;
    },
    onCloseConfirm() {
      this.visibleConfirm = false;
    },
    onClosePrompt() {
      this.visiblePrompt = false;
    }
  }
};
</script>

